import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { SharedModule } from 'src/app/shared/shared.module';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { ProductStockService } from 'src/app/service/product-stock.service';
import { ProductStockListComponent } from './product-stock-list/product-stock-list.component';
import { ProductStockSummaryListComponent } from './product-stock-summary-list/product-stock-summary-list.component';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { AddDamageProductComponent } from './add-damage-product/add-damage-product.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { NgSelectModule } from '@ng-select/ng-select';

export const routes: Routes = [
  {
    path: '',
    component: ProductStockListComponent,
    data: { title: 'product_stock_list' },
  },
  {
    path: 'summary',
    component: ProductStockSummaryListComponent,
    data: { title: 'product_summary_list' },
  },
  {
    path: 'add-damage',
    component: AddDamageProductComponent,
    data: { title: 'add_damage_product' }
  }
]
@NgModule({
  declarations: [ProductStockListComponent, ProductStockSummaryListComponent, AddDamageProductComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    SearchModule,
    SharedModule,
    BtnAddModule,
    FormsModule,
    NgSelectModule,
    PaginationModule,
    ReactiveFormsModule,
    FormValidationModule,
  ], providers: [ProductStockService]
})
export class ProductStockModule { }
