import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ProductStockService } from 'src/app/service/product-stock.service';

@Component({
  selector: 'app-add-damage-product',
  templateUrl: './add-damage-product.component.html',
})
export class AddDamageProductComponent implements OnInit {
  binLocation = ''
  addForm: FormGroup;
  showLoader = false;
  binArray: any = [];
  loadingState = false;
  productArray: any = [];
  selectedProductQty = 0;
  PageTitle = 'Add Damage Product'
  formErrors = { api_error: null, qty: null };

  constructor(
    private fb: FormBuilder,
    private location: Location,
    private productDamageService: ProductStockService,
  ) {
    this.addForm = this.fb.group({
      price: [null],
      status_id: [null],
      qty: [null, Validators.compose([Validators.required])],
      product_id: [null, Validators.compose([Validators.required])],
      location_detail: [null, Validators.compose([Validators.required])]
    });
  }

  ngOnInit(): void {
    this.getMasterData()
  }

  submitForm(formData: any): void {
    if (formData.valid) {
      this.showLoader = true;
      const data = formData.value
      data.location_code = this.binLocation
      this.productDamageService.addDamageProduct(formData.value).subscribe((response) => {
        this.showLoader = false;
        if (response.success) {
          this.back();
        } else {
          response.error.map(obj => {
            console.log(obj);
          });
        }
      }, (error) => {
        this.showLoader = false;
      });
    }
  }

  getMasterData(): void {
    this.productDamageService.getProductDamageMasterData().subscribe((response) => {
      this.binArray = response.data.bin
    });
  }

  getProducts(bin: any) {
    this.binLocation = bin.label
    this.productDamageService.getDamageProduct({ bin_id: bin.value.bin_id }).subscribe((response) => {
      this.productArray = response.data.product
    });
  }

  setProductObj(product) {
    this.selectedProductQty = product.quantity
  }


  quantityCheck() {
    if (Number(this.addForm.value.qty) <= this.selectedProductQty) {
      this.formErrors.qty = null;
      this.addForm.controls.qty.setErrors(null);
    }
    else {
      this.formErrors.qty = '* Please Enter equal or small value in Total Quantity';
      this.addForm.controls.qty.setErrors({ incorrect: true });
    }
  }

  back() {
    this.location.back();
  }

}
